<template>
  <input type="text" placeholder="이름" v-model="name"/>
  <input type="text" placeholder="클럽명" v-model="clubName"/>
  <button @Click="onClickAdd">추가</button>
  <br><br>
  <table border=1>
    <tr>
      <th class="text-center">이름</th>
      <th class="text-center">클럽명</th>
      <th class="text-center">삭제</th>
    </tr>
    <tr v-for="(member, index) in memberList" :key="index">
      <td>{{member.이름}}</td>
      <td>{{member.클럽명}}</td>
      <td><button @Click="removeBtn(member)">삭제</button></td>
    </tr>
  </table>
  <br>
  <br>
  <br>
</template>
<script>
import axios from 'axios'

export default {
  data: function () {
    return {
      name: '',
      clubName: '',
      memberList: []
    }
  },
  created: async function () {
    var result = await axios.get('http://115.140.64.101/api/users')
    this.memberList = result.data
  },
  mounted: async function () {
    console.log('test')
  },
  computed: {
  },
  methods: {
    onClickAdd: async function () {
      var param = {
        name: this.name,
        clubName: this.clubName
      }
      var result = await axios.post('http://115.140.64.101/api/user', param)
      if (result.data && result.data.customer_key) {
        this.memberList.push({ 이름: this.name, 클럽명: this.clubName, customer_key: result.data.customer_key })
        this.name = ''
        this.clubName = ''
      } else {
        alert('등록에 실패하였습니다.')
      }
    },
    removeBtn: async function (mem) {
      var param = {
        customerKey: mem.customer_key
      }
      var result = await axios.post('http://115.140.64.101/api/user/delete', param)
      if (result.data === 1) {
        this.memberList = this.memberList.filter(member => member !== mem)
      } else {
        alert('삭제에 실패하였습니다.')
      }
    }
  }
}
</script>
